import React from "react";
import api from '../../../services/api';
import { Grid, Paper, Typography } from "@material-ui/core";
import { withStyles } from '@material-ui/core/styles';
import Moment from 'react-moment';
import { Link } from "react-router-dom";

const useStyles = theme => ({

    containerPaciente: {
        display: 'flex',
        flexDirection: 'row',
        flexGrow: 1
    },

    contentImagem: {
        marginTop: 10,
        width: 80,
        height: 80,
        borderRadius: '50%',
        backgroundColor: '#228B22'
    },

    imagemPaciente: {
        width: 70,
        height: 70,
        borderRadius: '50%',
        marginLeft: 5,
        marginTop: 5
    },

    contentTexto: {
        marginLeft: 10
    },

    paper: {
        padding: theme.spacing(2),

    },
    link: {
        textDecoration: 'none'
    }
});

class Pacientes extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            pacientes: []
        }
    }

    componentDidMount = () => {
        var me = this;

        api.get('/pacientes')
            .then(response => me.setState({ pacientes: response.data }))
            .catch(function (error) {

            });
    }

    createComponent = (item) => {

        const { classes } = this.props;

        let dataAniversario = 'Não registrada';

        if (item.dataNascimento) {

            let dataNascimento = new Date() - new Date(item.dataNascimento + ' 00:00:00')

            let multiplicadorAnos = 1000 * 60 * 60 * 24 * 365;

            let anosNascimento = dataNascimento / multiplicadorAnos;

            let mesesNascimento = parseInt((anosNascimento - parseInt(anosNascimento)) * 12);

            dataAniversario = `${parseInt(anosNascimento)} anos e ${mesesNascimento} meses`;
        }

        return (
            <Grid item xs={12} md={6} lg={4}>
                <Link to={{
                    pathname: '/detalhes',
                    state: {
                        paciente: item.idPaciente
                    }
                }} className={classes.link}>
                    <Paper className={classes.paper}>
                        <div className={classes.containerPaciente}>
                            <div className={classes.contentImagem}>
                                <img src={item.fotoPaciente} className={classes.imagemPaciente} alt="Imagem Paciente" />
                            </div>
                            <div className={classes.contentTexto}>
                                <Typography component="h1" variant="subtitle1" color="primary" noWrap >
                                    {item.nome}
                                </Typography>
                                <Typography component="h1" variant="subtitle2" color="textSecondary" noWrap >
                                    <b>Código:</b> {item.idPaciente}
                                </Typography>
                                <Typography component="h1" variant="subtitle2" color="textSecondary" noWrap >
                                    <b>Idade:</b> {dataAniversario}
                                </Typography>
                                <Typography component="h1" variant="subtitle2" color="textSecondary" noWrap >
                                    <b>Cartão SUS:</b> {item.numeroCartaoSUS}
                                </Typography>
                                <Typography component="h1" variant="subtitle2" color="textSecondary" noWrap >
                                    <b>Data de Admissão:</b> <Moment element="span" format="DD/MM/YYYY">{item.dataAdmissao}</Moment>
                                </Typography>
                            </div>
                        </div>
                    </Paper>
                </Link>
            </Grid >
        );
    }

    render() {


        return (
            <div>
                <Grid container spacing={1}>
                    {
                        this.state.pacientes.map(item => this.createComponent(item))
                    }
                </Grid>
            </div>
        );
    }
}

export default withStyles(useStyles)(Pacientes)