import React from "react";
import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import { Toolbar, IconButton, Typography, TextField, Button } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import moment from 'moment';
import Pesquisa from "../../../components/pesquisa";
import api from '../../../services/api';
import { getUserId } from '../../../services/auth';

const useStyles = theme => ({
    root: {
        padding: theme.spacing(0),
        paddingLeft: theme.spacing(3)
    },

    toolbar: {
        padding: theme.spacing(0)
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },

    title: {
        textTransform: 'uppercase'
    },
    form: {
        display: 'flex',
        flexDirection: 'column',
        flexGrow: 1
    },
    footer: {
        paddingTop: theme.spacing(3),
        display: 'flex',
        marginLeft: "auto"
    }
});

class CadastroEvolucoes extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            open: false,
            dataEvolucao: moment().format('YYYY-MM-DD'),
            horaEvolucao: moment().format('HH:mm:ss'),
            evolucao: ''
        }
    }

    salvarEvolucao = () => {

        let me = this;

        const usuario = getUserId();
        const { dataEvolucao } = this.state;
        const { horaEvolucao } = this.state;
        const { evolucao } = this.state;
        const { paciente } = this.props;
        const { onSave } = this.props;

        api.post('/evolucao/salvar', {
            usuario: usuario,
            dataEvolucao: `${dataEvolucao} ${horaEvolucao}`,
            evolucao: evolucao,
            paciente: paciente
        }).then(() => {

            onSave();

            me.handleClose();


        }).catch(function (error) {

        });
    }

    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };
    handleChange = prop => event => {
        this.setState({ [prop]: event.target.value });
    };

    render() {

        const { classes } = this.props;

        const { open } = this.state;
        const { dataEvolucao } = this.state;
        const { horaEvolucao } = this.state;

        return (
            <Dialog
                fullWidth={true}
                maxWidth='md'
                open={open}
                onClose={this.handleClose}
                aria-labelledby="max-width-dialog-title"
            >
                <MuiDialogTitle disableTypography className={classes.root}>
                    <Toolbar className={classes.toolbar}>
                        <Typography component="h2" variant="subtitle1" color="primary" className={classes.title}>
                            Cadastro de Evoluções
                        </Typography>
                        <IconButton onClick={this.handleClose} className={classes.closeButton} >
                            <CloseIcon />
                        </IconButton>
                    </Toolbar>
                </MuiDialogTitle >
                <DialogContent>

                    <form className={classes.form} noValidate>
                        <TextField
                            id="data-evolução"
                            label="Data de Evolução"
                            type="date"
                            defaultValue={dataEvolucao}
                            onChange={this.handleChange("dataEvolucao")}
                            InputLabelProps={{
                                shrink: true,
                            }}
                        />
                        <TextField
                            id="hora-evolucao"
                            label="Hora da Evolução"
                            onChange={this.handleChange("horaEvolucao")}
                            type="time"
                            defaultValue={horaEvolucao}
                            className={classes.textField}
                            InputLabelProps={{
                                shrink: true,
                            }}
                        />
                        <Pesquisa
                            titulo='Pesquisa de Históricos'
                            codigoPesquisa={1}
                            rowsPerPage={20}
                            formato={(row) => `${row.idHistorico} - ${row.descricao}`}
                            configs={
                                [{
                                    title: 'Código',
                                    field: 'idHistorico',
                                    style: { width: 60 },
                                    format: (value) => value
                                }, {
                                    title: 'Descrição',
                                    field: 'descricao',
                                    style: { flex: 1 },
                                    format: (value) => value
                                }]
                            }
                            changeValue={(row) => this.setState({ evolucao: row.historico })}
                        />

                        <TextField
                            id="outlined-multiline-static"
                            label="Evolução"
                            multiline
                            value={this.state.evolucao}
                            onChange={this.handleChange("evolucao")}
                            rows="3"
                        />
                        <footer className={classes.footer}>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={this.salvarEvolucao}
                            >
                                Salvar
                        </Button>
                        </footer>
                    </form>

                </DialogContent>
            </Dialog>
        );
    }
}

export default withStyles(useStyles)(CadastroEvolucoes)