import React from "react";
import { withStyles } from '@material-ui/core/styles';
import Evolucoes from "./evolucoes";
import { Paper, Link } from "@material-ui/core";
import Title from '../../../components/title';
import { isAuthMenuView, isAuthMenuEdit } from '../../../services/auth';
import CadastroEvolucoes from './cadastroEvolucao';

const useStyles = theme => ({
    seeMore: {
        marginTop: theme.spacing(3),
    },
    paper: {
        padding: theme.spacing(2),
    },
});

class Detalhes extends React.Component {

    constructor(props) {
        super(props);

        this.modalCadastroEvolucao = React.createRef();
        this.gridEvolucoes = React.createRef();
    }

    cadastrarEvolucao = (event) => {
        event.preventDefault();

        this.modalCadastroEvolucao.current.handleOpen();
    }

    recarregarGridEvolucoes = () => {

        this.gridEvolucoes.current.recarregarEvolucoes();
    }

    render() {

        const { classes } = this.props;

        return (
            <Paper className={classes.paper}>
                {isAuthMenuView(16) &&
                    <div>
                        < Title > Evoluções do Paciente</Title>
                        <Evolucoes paciente={this.props.location.state.paciente} ref={this.gridEvolucoes} />
                        {isAuthMenuEdit(16) &&
                            <div className={classes.seeMore}>
                                <Link color="primary" href="#" onClick={this.cadastrarEvolucao}>
                                    Cadastrar Evolução
                                </Link>
                            </div>
                        }
                    </div>
                }
                <CadastroEvolucoes
                    ref={this.modalCadastroEvolucao}
                    paciente={this.props.location.state.paciente}
                    onSave={() => this.recarregarGridEvolucoes()}
                />
            </Paper>

        );
    }
}

export default withStyles(useStyles)(Detalhes)