import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";
import DashBoard from './pages/main';
import Login from "./pages/login";
import Pacientes from './pages/main/pacientes';
import Home from './pages/main/home'
import Detalhes from "./pages/main/detalhes";
import { isAuthenticated } from "./services/auth";
const routes = [
    {
        path: "/login",
        component: Login
    },
    {
        path: "/",
        component: DashBoard,
        auth: true,
        routes: [
            {
                path: "/home",
                title: 'Home',
                component: Home
            },
            {
                path: "/pacientes",
                title: 'Pacientes',
                codigoPrograma: 12,
                component: Pacientes
            },
            {
                path: "/detalhes",
                title: 'Detalhes',
                component: Detalhes
            }
        ]
    }
];

export default function RouteConfigExample() {
    return (
        <Router>
            <div>
                <Switch>
                    {routes.map((route, i) => (
                        <RouteWithSubRoutes key={i} {...route} />
                    ))}
                </Switch>
            </div>
        </Router>
    );
}

function RouteWithSubRoutes(route) {
    return (
        <Route
            path={route.path}
            render={props => {
         
                if (route.auth && !isAuthenticated()) {
                    return <Redirect to='/login' />
                }

                return <route.component {...props} routes={route.routes} />
            }}
        />
    );
}

