import React from "react";
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import api from '../../../services/api';

const rowsPerPage = 5;

export default class Evolucoes extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            page: 0,
            paciente: props.paciente,
            registros: 0,
            evolucoes: []
        }
    }

    componentDidMount() {

        this.buscarEvolucoes();
    }

    recarregarEvolucoes = () => {

        this.setState({ page: 0 }, () => this.buscarEvolucoes());
    }

    buscarEvolucoes = () => {

        let me = this;

        const { paciente } = this.state;
        const { page } = this.state;

        api.get('/paciente/evolucoes', {
            params: {
                codigoPaciente: paciente,
                pagina: page,
                registros: rowsPerPage
            }
        }).then(response => {

            let data = response.data;

            if (page === 0) {
                me.setState({ registros: data.numeroRegistros });
            }

            me.setState({ evolucoes: data.evolucaoPacientes });
        }).catch(function (error) {

        });
    }

    handleChangePage = (event, newPage) => {

        this.setState({ page: newPage }, () => this.buscarEvolucoes());
    };


    render() {

        const { page } = this.state;
        const { registros } = this.state;
        const { evolucoes } = this.state;

        return (
            <Paper>
                <TableContainer>
                    <Table size="small" aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Data de Evolucao</TableCell>
                                <TableCell>Funcionário</TableCell>
                                <TableCell>Evolução</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {evolucoes.map((row, index) => {
                                return (
                                    <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                                        <TableCell key='dataCadastro'>
                                            {row.dataCadastro}
                                        </TableCell>
                                        <TableCell key='funcionario'>
                                            {row.funcionario}
                                        </TableCell>
                                        <TableCell key='evolucao'>
                                            {row.evolucao}
                                        </TableCell>
                                    </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[5]}
                    labelDisplayedRows={({ from, to, count }) => `Exibindo registros ${from}-${to} de ${count}`}
                    component="div"
                    backIconButtonText='Página Anterior'
                    nextIconButtonText='Próxima Página'
                    count={registros}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={this.handleChangePage}
                />
            </Paper>
        );
    }
}
